@font-face {
  font-family: "Proxima-Nova";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ProximaNova-Light.woff2") format("woff2"), url("../fonts/ProximaNova-Light.woff") format("woff");
}
@font-face {
  font-family: "Proxima-Nova";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ProximaNova-Regularlt.woff2") format("woff2"), url("../fonts/ProximaNova-Regularlt.woff") format("woff");
}
@font-face {
  font-family: "Proxima-Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Proxima-Nova";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ProximaNova-Bold.woff2") format("woff2"), url("../fonts/ProximaNova-Bold.woff") format("woff");
}
@font-face {
  font-family: "El-Messiri";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ElMessiri-Bold.woff2") format("woff2"), url("../fonts/ElMessiri-Bold.woff") format("woff");
}
html {
  min-height: 100%;
}

body {
  background-color: #162831;
  padding: 0;
  margin: 0;
  min-width: 320px;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h5 {
  margin: 0;
}

.visualy-hidden {
  font-size: 0;
  opacity: 0;
  visibility: hidden;
  width: 0;
  height: 0;
  padding: 0;
}

.main-header__bg-hero {
  margin-top: 20px;
}

@media all and (max-width: 768px) {
  .main-nav {
    background-color: #000000;
  }
}
.main-nav__container {
  margin: 0 auto;
  max-width: 1296px;
  padding: 30px 30px;
  display: grid;
  grid-template-columns: min-content auto min-content;
  align-items: center;
  grid-column-gap: 50px;
}
@media all and (max-width: 768px) {
  .main-nav__container {
    grid-template-columns: min-content auto;
  }
}

.main-nav__link-logo {
  height: 60px;
  width: 60px;
}

.main-nav--border-bottom {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: 30px 0;
}

@media all and (max-width: 768px) {
  .main-nav__btn {
    display: none;
  }
}
.main-nav__burger-menu {
  margin-left: auto;
}

@media all and (max-width: 768px) {
  .main-nav__site-list {
    position: absolute;
    top: 120px;
    width: 100%;
    text-align: left;
    background: #306a6a;
    z-index: 1;
    flex-direction: column;
    align-items: center;
  }
  .main-nav__container--nojs .main-nav__site-list {
    position: static;
  }
  .main-nav__container--nojs {
    grid-template-columns: min-content auto min-content;
    grid-template-rows: auto auto;
    padding: 0;
    grid-column-gap: 0;
  }
  .main-nav__container--nojs .main-nav__burger-menu {
    display: none;
  }
  .main-nav__container--nojs .main-nav__site-list {
    grid-row: 2/3;
    grid-column: 1/-1;
  }
  .main-nav__container--nojs .main-nav__link-logo {
    padding: 30px;
    grid-column: 1/2;
  }
}
.site-list {
  padding: 0;
  margin: 0;
  left: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-self: center;
  min-width: 320px;
}

.site-list__item {
  margin-right: 50px;
}
@media all and (max-width: 768px) {
  .site-list__item {
    margin-right: 0;
    padding: 35px 0;
  }
}
.site-list__item:nth-last-child(-n+2) {
  margin-right: 0;
}

.site-list__link {
  font-family: "Proxima-Nova", sans-serif;
  font-size: 14px;
  color: #ffffff;
  font-weight: 400;
  display: block;
  line-height: 19px;
  padding: 5px 0;
}
@media all and (max-width: 768px) {
  .site-list__link {
    font-size: 25px;
    padding: 0;
  }
}

.site-list__item-btn {
  display: none;
}
@media all and (max-width: 768px) {
  .site-list__item-btn {
    display: block;
    border-top: 1px solid gray;
    padding: 40px 0;
  }
}

.site-list__btn {
  font-size: 25px !important;
}

.btn {
  font-family: "Proxima-Nova", sans-serif;
  font-weight: 600;
  padding: 9px 15px;
  border-radius: 3px;
  white-space: nowrap;
  font-size: 13px;
}
.btn--primary {
  color: #ffffff;
  background-color: #018abe;
}

.title {
  position: relative;
  color: #ffffff;
  font-family: "El-Messiri", sans-serif;
  font-weight: 700;
  font-size: 42px;
  line-height: 59px;
  display: inline-block;
}
.title::before {
  position: absolute;
  content: "";
  width: 28px;
  height: 14px;
  background-color: #018abe;
  border-radius: 0px 50px;
  top: 50%;
  left: -72px;
  transform: translateY(-50%);
}
@media all and (max-width: 768px) {
  .title::before {
    display: none;
  }
}
.title::after {
  position: absolute;
  content: "";
  width: 28px;
  height: 14px;
  background-color: #018abe;
  border-radius: 50px 0px;
  top: 50%;
  right: -72px;
  transform: translateY(-50%);
}
@media all and (max-width: 768px) {
  .title::after {
    display: none;
  }
}

.sessions {
  padding-top: 150px;
}

.sessions__container {
  margin: 0 auto;
  max-width: 1296px;
  padding: 0 30px;
  text-align: center;
}

.sessions__title {
  margin-bottom: 100px;
}

.sessions-card-block {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 258px);
  grid-auto-rows: 258px;
  grid-gap: 24px;
}
@media all and (max-width: 1200px) {
  .sessions-card-block {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media all and (max-width: 768px) {
  .sessions-card-block {
    grid-template-columns: 1fr;
  }
}

.sessions-card-block__item {
  border-radius: 10px;
  background: linear-gradient(0deg, rgba(1, 138, 190, 0.5), rgba(1, 138, 190, 0.5)), url("../img/women-under-water.jpg") no-repeat center/cover;
  text-align: left;
  display: flex;
  align-items: end;
  padding-bottom: 30px;
  padding-left: 40px;
}
.sessions-card-block__item--grid-row {
  grid-row: span 2;
  position: relative;
  padding-bottom: 50px;
  padding-left: 50px;
}
.sessions-card-block__item--grid-row::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 7px;
  bottom: 0;
  left: 0;
  background-color: #018abe;
  border-radius: 0px 0px 10px 10px;
}
@media all and (max-width: 768px) {
  .sessions-card-block__item--grid-row {
    grid-row: auto;
    grid-row: auto;
  }
}
@media all and (max-width: 768px) {
  .sessions-card-block__item {
    padding: 10px 20px;
  }
}

.sessions-card-block__title {
  font-family: "El-Messiri", sans-serif;
  font-weight: 700;
  color: #ffffff;
  font-size: 24px;
  line-height: 34px;
}
@media all and (max-width: 768px) {
  .sessions-card-block__title {
    font-size: 20px;
  }
}

.burger-menu {
  display: none;
}
@media all and (max-width: 768px) {
  .burger-menu {
    display: block;
    position: relative;
    height: 40px;
    width: 50px;
  }
}

.burger-menu__item {
  display: none;
}
@media all and (max-width: 768px) {
  .burger-menu__item {
    display: block;
    height: 4px;
    width: 100%;
    background-color: #ffffff;
    position: absolute;
  }
}

@media all and (max-width: 768px) {
  .burger-menu__item:first-child {
    top: 0;
  }
  .burger-menu__item:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
  }
  .burger-menu__item:last-child {
    bottom: 0;
  }
}
.bg-hero {
  padding: 0 30px;
}

.bg-hero__container {
  margin: 0 auto;
  max-width: 1296px;
  padding-top: 136px;
  background: linear-gradient(0deg, rgba(152, 201, 220, 0.5), rgba(152, 201, 220, 0.5)), url("../img/entry-img.jpg") no-repeat center/cover;
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  text-align: center;
  min-height: 600px;
  box-sizing: border-box;
}
@media all and (max-width: 768px) {
  .bg-hero__container {
    min-height: 370px;
    padding-top: 95px;
  }
}

.bg-hero__title {
  position: relative;
  color: #ffffff;
  font-family: "El-Messiri", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 34px;
  margin-bottom: 40px;
  display: inline-block;
}
.bg-hero__title::before {
  position: absolute;
  content: "";
  width: 28px;
  height: 14px;
  background-color: #ffffff;
  border-radius: 0px 50px;
  top: 50%;
  left: -94px;
  transform: translateY(-50%);
}
@media all and (max-width: 768px) {
  .bg-hero__title::before {
    display: none;
  }
}
.bg-hero__title::after {
  position: absolute;
  content: "";
  width: 28px;
  height: 14px;
  background-color: #ffffff;
  border-radius: 50px 0px;
  top: 50%;
  right: -94px;
  transform: translateY(-50%);
}
@media all and (max-width: 768px) {
  .bg-hero__title::after {
    display: none;
  }
}
@media all and (max-width: 768px) {
  .bg-hero__title {
    font-size: 20px;
  }
}

.bg-hero__main-title {
  color: #ffffff;
  font-size: 64px;
  font-family: "El-Messiri", sans-serif;
  line-height: 90px;
  font-weight: 700;
}
@media all and (max-width: 768px) {
  .bg-hero__main-title {
    line-height: 50px;
    font-size: 35px;
  }
}

.reviews {
  padding-top: 150px;
}

.reviews__container {
  text-align: center;
  max-width: 1736px;
  margin: 0 auto;
  padding: 0 60px;
}

.reviews__title {
  margin-bottom: 98px;
}

.carousel__item {
  text-align: left;
  padding: 40px 45px 71px 45px;
  background-color: #018abe;
  margin: 0 25px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  opacity: 0.5;
  box-sizing: border-box;
  width: 500px;
}
@media all and (max-width: 768px) {
  .carousel__item {
    padding: 25px 20px 25px;
  }
}

.carousel__bit-quote {
  margin: 0 0 10px;
  display: inline-block;
}
.carousel__bit-quote:last-child {
  margin-bottom: 0;
}

.carousel__author {
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  font-family: "Proxima-Nova", sans-serif;
  color: #ffffff;
  font-style: normal;
  display: block;
  margin-bottom: 15px;
}

.carousel__quote {
  color: #ffffff;
  font-size: 10px;
  font-weight: 300;
  line-height: 14px;
  font-family: "Proxima-Nova", sans-serif;
  margin: 0;
}

.carousel__item.slick-current {
  opacity: 1;
  width: 636px;
  padding: 50px 60px 56px 60px;
}

.carousel__item.slick-current .carousel__author {
  margin-bottom: 20px;
}

.carousel__item.slick-current .carousel__author {
  font-size: 20px;
  line-height: 28px;
}

.carousel__item.slick-current .carousel__quote {
  font-size: 14px;
  line-height: 20px;
}

.slick-list {
  margin: 0 -25px;
}

.slick-track {
  display: flex;
  align-items: center;
}

.slick-dots {
  margin-top: 60px;
  position: static;
}

.slick-dots li {
  padding: 0;
  width: 16px;
  height: 16px;
  margin-right: 20px;
  margin-left: 0;
}
.slick-dots li:last-child {
  margin-right: 0;
}

li.slick-active button::before {
  opacity: 1 !important;
}

.slick-dots button {
  padding: 0 !important;
  width: 100% !important;
  height: 100% !important;
}
.slick-dots button::before {
  content: "" !important;
  background-color: #ffffff;
  width: 100% !important;
  height: 100% !important;
  border-radius: 3px;
  opacity: 0.4 !important;
}

.about-section {
  padding-top: 124px;
}

.about-section__container {
  text-align: center;
  margin: 0 auto;
  max-width: 1076px;
  padding: 0 30px;
}

.about-section__title {
  margin-bottom: 99px;
}
@media all and (max-width: 768px) {
  .about-section__title {
    margin-bottom: 50px;
  }
}

.about-section__card {
  display: grid;
  grid-template-columns: auto auto;
  grid-column-gap: 84px;
  align-items: center;
  margin-bottom: 99px;
}
.about-section__card:last-child {
  margin-bottom: 0;
}
@media all and (max-width: 1200px) {
  .about-section__card {
    grid-column-gap: 50px;
  }
}
@media all and (max-width: 768px) {
  .about-section__card {
    grid-template-columns: 1fr;
    margin-bottom: 60px;
  }
}

.about-section__img {
  width: 100%;
  height: 365px;
  border-radius: 10px;
  object-fit: cover;
}
@media all and (max-width: 768px) {
  .about-section__img {
    width: 100%;
    margin-bottom: 30px;
    height: 235px;
  }
}

.about-section__card:last-of-type .about-section__img {
  height: 400px;
}
@media all and (max-width: 768px) {
  .about-section__card:last-of-type .about-section__img {
    height: 235px;
  }
}

.about-section__card:first-of-type .about-section__card-content {
  order: -1;
}
@media all and (max-width: 768px) {
  .about-section__card:first-of-type .about-section__card-content {
    order: 0;
  }
}

.about-section__card-content {
  text-align: left;
  max-width: 466px;
}
@media all and (max-width: 768px) {
  .about-section__card-content {
    max-width: 100%;
  }
}

.about-section__title-card-content {
  font-family: "Proxima-Nova", sans-serif;
  color: #ffffff;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 30px;
}

.about-description-card-content {
  font-family: "Proxima-Nova", sans-serif;
  font-weight: 300;
  font-size: 16px;
  color: #ffffff;
  line-height: 22px;
}

.price {
  padding-top: 140px;
  text-align: center;
}

.price__container {
  max-width: 1076px;
  margin: 0 auto;
  padding: 0 30px;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-column-gap: 134px;
  align-items: center;
  padding-top: 100px;
}
@media all and (max-width: 768px) {
  .price__container {
    grid-template-columns: 1fr;
  }
}

.price__img {
  border-radius: 10px;
  max-width: 100%;
  height: auto;
  margin-bottom: 30px;
  display: block;
}
@media all and (max-width: 768px) {
  .price__img {
    margin: 50px auto 30px;
  }
}

.price__description {
  color: #ffffff;
  font-size: 21px;
  font-family: "El-Messiri", sans-serif;
  line-height: 29px;
  font-weight: 700;
}

.table {
  text-align: left;
  width: 100%;
}

.table__row {
  vertical-align: baseline;
}

.table__cell-title {
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  font-family: "Proxima-Nova", sans-serif;
  color: #ffffff;
  padding-left: 37px;
  position: relative;
}
.table__cell-title::before {
  position: absolute;
  content: "";
  width: 18px;
  height: 9px;
  background-color: #018abe;
  top: 9px;
  left: 0;
  border-radius: 0px 50px;
}

.table__cell {
  color: #ffffff;
  font-size: 18px;
  line-height: 25px;
  font-weight: 700;
  font-family: "El-Messiri", sans-serif;
  text-align: right;
}

.table__cell-title {
  padding-bottom: 31px;
  padding-right: 35px;
}

.table__row:last-child .table__cell-title {
  padding-bottom: 0;
}

.contacts {
  padding-top: 150px;
  padding-bottom: 60px;
}

.contacts__container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1296px;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto;
  grid-column-gap: 48px;
  grid-row-gap: 24px;
}
@media all and (max-width: 1200px) {
  .contacts__container {
    grid-template-columns: 1fr 1fr;
  }
}
@media all and (max-width: 768px) {
  .contacts__container {
    grid-template-columns: 1fr;
  }
}

.contacts__map {
  display: block;
  grid-column: 1/2;
  grid-row: 1/3;
  border-radius: 10px;
  border: 0;
  min-height: 100%;
  max-width: 100%;
}
@media all and (max-width: 1200px) {
  .contacts__map {
    grid-column: 1/-1;
    width: 100%;
  }
}
@media all and (max-width: 768px) {
  .contacts__map {
    height: auto;
    min-height: 350px;
  }
}

.contacts-block {
  background-color: #018abe;
  padding: 35px 45px;
  border-radius: 10px;
}
@media all and (max-width: 768px) {
  .contacts-block {
    padding: 35px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.contacts-block__title {
  color: #ffffff;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
  font-family: "El-Messiri", sans-serif;
  margin-bottom: 20px;
}
@media all and (max-width: 768px) {
  .contacts-block__title {
    text-align: center;
  }
}

.contacts-block__address, .contacts-block__record {
  display: flex;
  align-items: center;
  color: #ffffff;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  font-family: "Proxima-Nova", sans-serif;
  font-style: normal;
}

.contacts-block__address {
  margin-bottom: 21px;
}

.contacts-block__record {
  margin-bottom: 40px;
}

.contacts-block__address-svg {
  margin-right: 20px;
}

.contacts-block__link {
  background-color: #ffffff;
  color: #018abe;
}

.main-footer {
  padding: 30px 0;
}

.main-footer__container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1296px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.main-footer__copyright, .main-footer__author {
  margin: 0;
  color: #ffffff;
  font-size: 14px;
  line-height: 19px;
  font-weight: 400;
  font-family: "Proxima-Nova", sans-serif;
}

.main-footer__copyright {
  margin-right: 55px;
}

.main-footer__author {
  color: #ffffff;
  font-size: 14px;
  line-height: 19px;
  font-weight: 700;
  font-style: normal;
  font-family: "Proxima-Nova", sans-serif;
}

.main-footer__time {
  margin: 0 3px;
}